import AccordionMy from './Accordion';
import ComponentBoxWithRoot from './Containers/ComponentBoxWithRoot';
import Tooltip from '@mui/material/Tooltip';
const ResponsiveGridLayout = WidthProvider(Responsive);

function isEmptyObject(obj) {//    
  for (var i in obj) {//   
      if (obj.hasOwnProperty(i)) {//   ?
          return false;
      }
  }
  return true;
  
}

const useStyles = makeStyles({//  /
  list: {
    width: 250,
  },
  fullList: {
    width: 'auto',
  },
  container :{
    position: 'relative',
    color: theme.palette.common.white,
    backgroundSize:"cover",
    backgroundRepeat:"no-repeat",
    backgroundPosition:"center",
  },
  fab: {
    margin: 0,
    top: 'auto',
    right: 20,
    bottom: "40%",
    left: 'auto',
    position: 'fixed',
  },
});


const Wrapper = () => {
    var SelectedFilesRCData= {};
    const classes = useStyles(); // 
    const theme = useTheme(); //   UeseTheme  
    const originalLayouts = getFromLS("layouts") || {};//    
    const originalLayoutComponents = JSON.parse(global.localStorage.getItem("components"));//    
    //  =  
    const [layoutsTest, setLayoutsTest] = React.useState({ layouts: JSON.parse(JSON.stringify(originalLayouts)) }  )//     [,    ] = useState(  )
    const [breakPointCurrent, setBreakPointCurrent] = React.useState(ReturnCurrentBreakpoin());//      Grid
    const [counter,setCounter]=React.useState(Number(global.localStorage.getItem("count")));//    
    const [radioFirts, setRadioFirst] = React.useState(undefined);//       
    const [state, setState] = React.useState({ left: false});//true/false     
    const [Edit, setEdit] = React.useState({ left: false});//   
    const [layoutComponents, setLayoutcomponents] = React.useState(originalLayoutComponents);//      ,  
    const [error, setError] = React.useState("");//   
    const [adminMode, setAdminMode] = React.useState(true);///  
    const [FirstLoad, setFirstLoad] = React.useState(true)//    
    const [currentEdit, setCurrentEdit] = React.useState("");//    
    const [selectedFiles,setSelectedFiles] = React.useState({});//   
    

    const breakpoints = ["lg", "md","sm","xs","xxs"]

    const toggleDrawer = (anchor, open) => (event) => {//    Drawer 
      if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {//         
        return;
      }
      setState({ ...state, [anchor]: open });//    
    };

    React.useEffect(()=>{  //       ,      layoutComponents 
      let json={"lg":{},"md":{},"sm":{}, "xs":{},"xxs":{} };//      
      if( layoutComponents ===null ){
        setLayoutcomponents( json)//   
        global.localStorage.setItem("components",JSON.stringify(layoutComponents));//  LS
      }
    },[layoutComponents])



    React.useEffect(()=>{//       counter    
      global.localStorage.setItem("count","");
      if( counter === null){// counter === null      LS,     null 
        global.localStorage.setItem("count",0);//   LS 
        setCounter(0);
      }else{
        global.localStorage.setItem("count",Number(counter));
      }
      
    },[counter])
  
    const handleChangeSwitch = (event) => {//  
      setAdminMode(event.target.checked);// event   
    };

    React.useEffect(()=>{//          ,    /  
      if(!FirstLoad){//    
        let newStatickLayout = {"lg":[], "md":[],"sm":[], "xs":[],"xxs":[]  }
        for (const [key, value] of Object.entries(layoutsTest.layouts)) {//  
          
          for (const [key2, value2] of Object.entries(value)) {//     
            
            newStatickLayout[key].push(
              {
                "w":value2.w,"h":value2.h,"x":value2.x,"y":value2.y ,"i":value2.i,"static": !adminMode, "minW": 2,//        / 
              }
            )
          }
          
        }
        setLayoutsTest({layouts: newStatickLayout}) //   ,     
      }else{
        setFirstLoad(false)
      }
    },[adminMode])

    const ChoseComponentList = (anchor) => (//  const        
      <div className={clsx(classes.list, {
          [classes.fullList]: anchor === 'top' || anchor === 'bottom',
        })}
      >
        <Grid container direction="column"  justifyContent="center"  alignItems="center" spacing={2}>
          
            <Grid item style={{paddingTop:"10%", marginLeft:"6%"}}>
              <FormControl component="fieldset">
                
                <FormLabel component="legend"> </FormLabel>{/*     */}
                <RadioGroup  value={radioFirts} onChange={handleChangeFirstRadio}>
                  <FormControlLabel value="Paragraph" control={<Radio />} label="" />
                  <FormControlLabel value="CardNews1" control={<Radio />} label="y" />
                  <FormControlLabel value="CardMap1" control={<Radio />} label=" " />
                  <FormControlLabel value="ProductHero" control={<Radio />} label=" " />
                  <FormControlLabel value="ProductHowItWorks" control={<Radio />} label="  ?" />
                  <FormControlLabel value="ProductSmokingHero" control={<Radio />} label="-" />
                  <FormControlLabel value="ProductCTA" control={<Radio />} label="  ()" />
                  <FormControlLabel value="CardBoard" control={<Radio />} label="" />
                  <FormControlLabel value="Comments" control={<Radio />} label="" />
                  <FormControlLabel value="FormCallMeBackSecond" control={<Radio />} label="  (+)" />
                  <FormControlLabel value="CardMapCustom1" control={<Radio />} label="  " />
                  <FormControlLabel value="Accordion" control={<Radio />} label="-" />
                </RadioGroup>
              </FormControl>
              <Grid item style={{ color:"red"}}>
                {error !== null ? `${error}` : ""}
              </Grid>
            </Grid>
            <Grid item>
              <Button onClick={addLayout}>{/*  */}
                 
              </Button>
            </Grid>
            
        </Grid>
        
      </div>
    );

  

    function SaveChanges(event){ //       
      event.preventDefault();// event      
      const data = new FormData(event.currentTarget);//  ,      
      let currentEditComponent = layoutComponents[breakPointCurrent][currentEdit];//      ,    
      let EditedJSON = ReturnEditedJson(currentEditComponent, data)//        JSON   
      let OldLayoutsPlusEdited =layoutComponents;//     (  ) +   
      for (const [key, value] of Object.entries(layoutComponents)) {//  
        for (const [key2, value2] of Object.entries(value)) {//     
          if(key2 ===currentEdit){
            OldLayoutsPlusEdited[key]= Object.assign(value,{[currentEdit]:EditedJSON})//   + 
          }
        }
      }
      setLayoutcomponents(OldLayoutsPlusEdited)//     
      global.localStorage.setItem("components",JSON.stringify(OldLayoutsPlusEdited));//   LS
    }

    function ReturnEditedJson(element, data){//     -   ,   switch Case    
      switch(element.component){
        case"Paragraph":
          return {"component":element.component,"mainTitle": data.get("mainTitle"), "mainContent":data.get("mainContent") ,imgBG: isUndefinedImage(selectedFiles.imgBG, true, element.imgBG)}
        case "CardNews1":
          return {"component":element.component,
          "mainTitle": data.get("mainTitle"), 
          "mainContent1":data.get("mainContent1") , 
          "mainContent2":data.get("mainContent2") , 
          img1: isUndefinedImage(selectedFiles.img1, false, element.img1),
          img2: isUndefinedImage(selectedFiles.img2, false, element.img2),
          imgBG: isUndefinedImage(selectedFiles.imgBG, true, element.imgBG)}
        case"CardMap1":
          return {"component":element.component,
                  "mainTitle1": data.get("mainTitle1"),
                  "mainTitle2": data.get("mainTitle2"),
                  "mainTitle3": data.get("mainTitle3"), 
                  "mainContent1":data.get("mainContent1") ,
                  "mainContent2":data.get("mainContent2"),
                  "mainContent3":data.get("mainContent3"), 
                  img1: isUndefinedImage(selectedFiles.img1, false, element.img1),
                  img2: isUndefinedImage(selectedFiles.img2, false, element.img2),
                  img3: isUndefinedImage(selectedFiles.img3, false, element.img3),
                  imgBG: isUndefinedImage(selectedFiles.imgBG, true, element.imgBG)
                }
        case"ProductHero":
          return {"component":element.component,"mainTitle": data.get("mainTitle"), imgBG: isUndefinedImage(selectedFiles.imgBG, true , element.imgBG)}
        case"ProductValues":
          return {"component":element.component,
                  "mainTitle1": data.get("mainTitle1"),
                  "mainTitle2": data.get("mainTitle2"),
                  "mainTitle3": data.get("mainTitle3"), 
                  "mainContent1":data.get("mainContent1") ,
                  "mainContent2":data.get("mainContent2"),
                  "mainContent3":data.get("mainContent3"), 
                  imgBG: isUndefinedImage(selectedFiles.imgBG, true , element.imgBG)
                }
        case"ProductHowItWorks":
          return {"component":element.component,"mainTitle": data.get("mainTitle"),
          "mainContent1":data.get("mainContent1") ,
          "mainContent2":data.get("mainContent2"),
          "mainContent3":data.get("mainContent3"), 
           imgBG: isUndefinedImage(selectedFiles.imgBG, true , element.imgBG)} 
        case"ProductSmokingHero":
          return {"component":element.component,"mainTitle": data.get("mainTitle"), "mainContent":data.get("mainContent"), imgBG: isUndefinedImage(selectedFiles.imgBG, true, element.imgBG) }
        case"ProductCTA":
          return {"component":element.component,"mainTitle":data.get("mainTitle"), "mainContent":data.get("mainContent") , imgBG: isUndefinedImage(selectedFiles.imgBG, true, element.imgBG), img1: isUndefinedImage(selectedFiles.img1, true, element.img1),}
        case"CardBoard":
          return {"component":element.component,
          "mainTitle1": data.get("mainTitle1") , "mainContent1":data.get("mainContent1")  ,
          "mainTitle2": data.get("mainTitle2") , "mainContent2":data.get("mainContent2")  , 
          "mainTitle3": data.get("mainTitle3") , "mainContent3":data.get("mainContent3")  , 
          "mainTitle4": data.get("mainTitle4") , "mainContent4":data.get("mainContent4")  , 
          "mainTitle5": data.get("mainTitle5") , "mainContent5":data.get("mainContent5")  , 
          "mainTitle6": data.get("mainTitle6") , "mainContent6":data.get("mainContent6")  , 
          "mainTitle7": data.get("mainTitle7") , "mainContent7":data.get("mainContent7")  , 
          "mainTitle8": data.get("mainTitle8") , "mainContent8":data.get("mainContent8")  ,  
          imgBG: isUndefinedImage(selectedFiles.imgBG, true , element.imgBG) }
        case"Comments":
          return {"component":element.component,
          "mainTitle1": data.get("mainTitle1") , "mainContent1":data.get("mainContent1")  ,
          "mainTitle2": data.get("mainTitle2") , "mainContent2":data.get("mainContent2")  , 
          "mainTitle3": data.get("mainTitle3") , "mainContent3":data.get("mainContent3")  , 
          img1:isUndefinedImage(selectedFiles.img1, false, element.img1),  
          img2: isUndefinedImage(selectedFiles.img2, false, element.img2),
          img3:isUndefinedImage(selectedFiles.img3, false, element.img3),
          imgBG:isUndefinedImage(selectedFiles.imgBG, true , element.imgBG) }
        case"FormCallMeBackSecond":
          return {"component":element.component,imgBG:isUndefinedImage(selectedFiles.imgBG, true , element.imgBG) }
        case"CardMapCustom1":
          return {"component":element.component,
          "mainTitle": data.get("mainTitle"), "mainContent":data.get("mainContent") ,
          "mainTitle1": data.get("mainTitle1") , "mainContent1":data.get("mainContent1")  ,
          "mainTitle2": data.get("mainTitle2") , "mainContent2":data.get("mainContent2")  , 
          "mainTitle3": data.get("mainTitle3") , "mainContent3":data.get("mainContent3")  , 
          "mainTitle4": data.get("mainTitle4") , "mainContent4":data.get("mainContent4")  , 
          "mainTitle5": data.get("mainTitle5") , "mainContent5":data.get("mainContent5")  , 
          "mainTitle6": data.get("mainTitle6") , "mainContent6":data.get("mainContent6")  , 
          imgBG:isUndefinedImage(selectedFiles.imgBG, true , element.imgBG) ,
           img1:isUndefinedImage(selectedFiles.img1, false, element.img1),  img2: isUndefinedImage(selectedFiles.img2, false, element.img2),
           img3:isUndefinedImage(selectedFiles.img3, false, element.img3),  img4:isUndefinedImage(selectedFiles.img4, false, element.img4),
           img5:isUndefinedImage(selectedFiles.img5, false, element.img5),  img6: isUndefinedImage(selectedFiles.img6, false, element.img6),}
        case"Accordion":
          return {"component":element.component,
          "mainTitle1": data.get("mainTitle1") , "mainContent1":data.get("mainContent1")  ,
          "mainTitle2": data.get("mainTitle2") , "mainContent2":data.get("mainContent2")  , 
          "mainTitle3": data.get("mainTitle3") , "mainContent3":data.get("mainContent3")  , 
          imgBG: isUndefinedImage(selectedFiles.imgBG, true , element.imgBG)}
        }
    }

    function isUndefinedImage(img,BG ,element){//      ,      url()     
      if(img === undefined){
        return element
      }else{
        return BG?  `url(${img})`: img
      }
    }

    const EditText = (anchor) => {//   ,     
      if(currentEdit!== ""){
        
        let currentEditComponent = layoutComponents[breakPointCurrent][currentEdit];
        return(
          <div className={clsx(classes.list, {
              [classes.fullList]: anchor === 'top' || anchor === 'bottom',
            })}
          >
            <Grid container direction="column"  justifyContent="center"  alignItems="center" spacing={2}>
               
                <Grid item style={{paddingTop:"10%"}}>
                  <Box component="form" onSubmit={SaveChanges}>
                    ID : {currentEdit}
                    {/*  ,       .   maintitle  ,      */}
                    <Grid container spacing={2} justifyContent="center" direction="column" alignItems="center" >
                      {currentEditComponent.mainTitle?<Grid item xs={12} sm={12}><TextField id="mainTitle"  label="" name="mainTitle"   multiline    defaultValue={currentEditComponent.mainTitle} variant="filled"/></Grid>:<></>}
                      {currentEditComponent.mainContent?<Grid item xs={12} sm={12}><TextField id="mainContent" label="" name="mainContent"     multiline   rows={4} defaultValue={currentEditComponent.mainContent} variant="filled"/></Grid>:<></>}
                      {currentEditComponent.mainTitle1?<Grid item xs={12} sm={12}><TextField id="mainTitle1"  label=" 1" name="mainTitle1"  multiline    defaultValue={currentEditComponent.mainTitle1} variant="filled"/></Grid>:<></>}
                      {currentEditComponent.mainContent1?<Grid item xs={12} sm={12}><TextField id="mainContent1" label="  1" name="mainContent1" multiline   rows={4} defaultValue={currentEditComponent.mainContent1} variant="filled"/></Grid>:<></>}
                      {currentEditComponent.mainTitle2?<Grid item xs={12} sm={12}><TextField id="mainTitle2"  label=" 2" name="mainTitle2" multiline  defaultValue={currentEditComponent.mainTitle2} variant="filled"/></Grid>:<></>}
                      {currentEditComponent.mainContent2?<Grid item xs={12} sm={12}><TextField id="mainContent2" label="  2" name="mainContent2" multiline   rows={4} defaultValue={currentEditComponent.mainContent2} variant="filled"/></Grid>:<></>}
                      {currentEditComponent.mainTitle3?<Grid item xs={12} sm={12}><TextField id="mainTitle3"  label=" 3"  name="mainTitle3" multiline   defaultValue={currentEditComponent.mainTitle3} variant="filled"/></Grid>:<></>}
                      {currentEditComponent.mainContent3?<Grid item xs={12} sm={12}><TextField id="mainContent3" label="  3" name="mainContent3" multiline   rows={4} defaultValue={currentEditComponent.mainContent3} variant="filled"/></Grid>:<></>}
                      {currentEditComponent.mainTitle4?<Grid item xs={12} sm={12}><TextField id="mainTitle4"  label=" 4"  name="mainTitle4" multiline   defaultValue={currentEditComponent.mainTitle4} variant="filled"/></Grid>:<></>}
                      {currentEditComponent.mainContent4?<Grid item xs={12} sm={12}><TextField id="mainContent4" label="  4" name="mainContent4" multiline   rows={4} defaultValue={currentEditComponent.mainContent4} variant="filled"/></Grid>:<></>}
                      {currentEditComponent.mainTitle5?<Grid item xs={12} sm={12}><TextField id="mainTitle5"  label=" 5"  name="mainTitle5" multiline   defaultValue={currentEditComponent.mainTitle5} variant="filled"/></Grid>:<></>}
                      {currentEditComponent.mainContent5?<Grid item xs={12} sm={12}><TextField id="mainContent5" label="  5" name="mainContent5" multiline   rows={4} defaultValue={currentEditComponent.mainContent5} variant="filled"/></Grid>:<></>}
                      {currentEditComponent.mainTitle6?<Grid item xs={12} sm={12}><TextField id="mainTitle6"  label=" 6"  name="mainTitle6" multiline   defaultValue={currentEditComponent.mainTitle6} variant="filled"/></Grid>:<></>}
                      {currentEditComponent.mainContent6?<Grid item xs={12} sm={12}><TextField id="mainContent6" label="  6" name="mainContent6" multiline   rows={4} defaultValue={currentEditComponent.mainContent6} variant="filled"/></Grid>:<></>}
                      {currentEditComponent.mainTitle7?<Grid item xs={12} sm={12}><TextField id="mainTitle7"  label=" 7"  name="mainTitle7" multiline   defaultValue={currentEditComponent.mainTitle7} variant="filled"/></Grid>:<></>}
                      {currentEditComponent.mainContent7?<Grid item xs={12} sm={12}><TextField id="mainContent7" label="  7" name="mainContent7" multiline   rows={4} defaultValue={currentEditComponent.mainContent7} variant="filled"/></Grid>:<></>}
                      {currentEditComponent.mainTitle8?<Grid item xs={12} sm={12}><TextField id="mainTitle8"  label=" 8"  name="mainTitle8" multiline   defaultValue={currentEditComponent.mainTitle8} variant="filled"/></Grid>:<></>}
                      {currentEditComponent.mainContent8?<Grid item xs={12} sm={12}><TextField id="mainContent8" label="  8" name="mainContent8" multiline   rows={4} defaultValue={currentEditComponent.mainContent8} variant="filled"/></Grid>:<></>}

                    
                      <Grid item xs={12} sm={12} style={{ marginLeft:"12%"}}>
                        {/*    input ,     */}
                        {ReturnInputs(currentEditComponent)}
                      </Grid>
                      <Grid item xs={12} sm={12}>
                        <Button type="submit" >
                          
                        </Button>
                      </Grid>
                    </Grid>
                  </Box>
                </Grid>
                <Grid item>
                  
                </Grid>
                
            </Grid>
            
          </div>
        )
      }
    }

    function ReturnInputs(currentEditComponent){
      let returnInputsJSX =[]
      for (const [key, value] of Object.entries(currentEditComponent)) {//   
        if(key.substr(0, 3)=== "img"){//  3   img,  
          returnInputsJSX.push(
            <div>
              <label for="file">{key}</label>
              <input type={"file"} id={key} onChange={handleFile} accept="image/png, image/jpeg"/>
            </div>
            
          )
        }
      }
      return returnInputsJSX//     
    }

    function handleFile(event){//  ,    base64   
      let   rcdata;
      const id = event.currentTarget.getAttribute("id");// id ,      
      let file = event.target.files[0]//  ,  
      let reader = new FileReader(); //    FileReader
      reader.readAsDataURL(file) //    
      reader.onload=(event)=>{//   

        
        rcdata = Object.assign(selectedFiles,{[id]:event.target.result})//   base 64 
        selectedFiles({...selectedFiles,rcdata })//      
        
      }

    }
    
    function handleChangeFirstRadio(event){//   ,    
      setRadioFirst(event.target.value)
    }

    function ReturnCurrentBreakpoin(){//       
      const innerWidth = window.innerWidth;//     
      if(innerWidth < 521){//      
        return 'xxs';
      }else if(innerWidth < 809){
        return 'xs';
      }else if(innerWidth <1037){
        return 'sm';
      }else if(innerWidth < 1241){
        return 'md';
      }else{
        return 'lg';
      }
    }

    function ReturnComponentBasedOnId(){//      + .     
      let NewComponentsPlusOld={"lg":{}, "md":{},"sm":{}, "xs":{},"xxs":{}  }
      for (const [key, value] of Object.entries(layoutComponents)) {
        NewComponentsPlusOld[key] = Object.assign({},{[Number(counter)]: HowManyContent(radioFirts)},value)
      }
      setLayoutcomponents(NewComponentsPlusOld)
      global.localStorage.setItem("components",JSON.stringify(NewComponentsPlusOld));
    }

    function HowManyContent(WhichComponent){//    ,    
      switch(WhichComponent){
        case"Paragraph":
          return {"component":radioFirts,"mainTitle": Content.LoremContent, "mainContent":Content.LoremContent3 ,imgBG: `url(https://picsum.photos/1920/1080)`}
        case "CardNews1":
          return {"component":radioFirts,
                  "mainTitle": Content.LoremContent, 
                  "mainContent1":Content.LoremContent3,
                  "mainContent2":Content.LoremContent3, 
                  imgBG: `url(https://picsum.photos/1920/1080)`,
                  img1: `https://picsum.photos/500/500`,
                  img2: `https://picsum.photos/400/400`,
                  }
        case"CardMap1":
          return {"component":radioFirts,
                  "mainTitle1": Content.LoremContent,
                  "mainTitle2": Content.LoremContent,
                  "mainTitle3": Content.LoremContent, 
                  "mainContent1":Content.LoremContent3 ,
                  "mainContent2":Content.LoremContent3 ,
                  "mainContent3":Content.LoremContent3 ,  
                  img1: `https://picsum.photos/400/400`,
                  img2: `https://picsum.photos/400/400`,
                  img3: `https://picsum.photos/400/400`,
                  imgBG: `url(https://picsum.photos/1920/1080)`
                }
        case"ProductHero":
          return {"component":radioFirts,"mainTitle": Content.LoremContent, imgBG: `url(https://picsum.photos/1920/1080)`}
        case"ProductValues":
          return {"component":radioFirts,
                  "mainTitle1": Content.LoremContent,
                  "mainTitle2": Content.LoremContent,
                  "mainTitle3": Content.LoremContent, 
                  "mainContent1":Content.LoremContent3 ,
                  "mainContent2":Content.LoremContent3 ,
                  "mainContent3":Content.LoremContent3 , 
                  imgBG: `url(https://picsum.photos/1920/1080)`
                }
        case"ProductHowItWorks":
          return {"component":radioFirts,"mainTitle": Content.LoremContent,
          "mainContent1":Content.LoremContent3 ,
          "mainContent2":Content.LoremContent3 ,
          "mainContent3":Content.LoremContent3 , 
           imgBG: `url(https://picsum.photos/1920/1080)`} 
        case"ProductSmokingHero":
          return {"component":radioFirts,"mainTitle": Content.LoremContent, "mainContent":Content.LoremContent3, imgBG: `url(https://picsum.photos/1920/1080)` }
        case"ProductCTA":
          return {"component":radioFirts,"mainTitle": Content.LoremContent, "mainContent":Content.LoremContent3 , imgBG: `url(https://picsum.photos/1920/1080)` , img1: `url(https://picsum.photos/500/500)`}
        case"CardBoard":
          return {"component":radioFirts,
          "mainTitle1": Content.LoremContent, "mainContent1":Content.LoremContent3 ,
          "mainTitle2": Content.LoremContent, "mainContent2":Content.LoremContent3 , 
          "mainTitle3": Content.LoremContent, "mainContent3":Content.LoremContent3 , 
          "mainTitle4": Content.LoremContent, "mainContent4":Content.LoremContent3 , 
          "mainTitle5": Content.LoremContent, "mainContent5":Content.LoremContent3 , 
          "mainTitle6": Content.LoremContent, "mainContent6":Content.LoremContent3 , 
          "mainTitle7": Content.LoremContent, "mainContent7":Content.LoremContent3 , 
          "mainTitle8": Content.LoremContent, "mainContent8":Content.LoremContent3 ,  
          imgBG: `url(https://picsum.photos/1920/1080)` }
        case"Comments":
          return {"component":radioFirts,
          "mainTitle1": Content.LoremContent, "mainContent1":Content.LoremContent3 ,
          "mainTitle2": Content.LoremContent, "mainContent2":Content.LoremContent3 , 
          "mainTitle3": Content.LoremContent, "mainContent3":Content.LoremContent3 ,  
          img1: `https://picsum.photos/400/400`,
          img2: `https://picsum.photos/400/400`,
          img3: `https://picsum.photos/400/400`,
          imgBG: `url(https://picsum.photos/1920/1080)` }
        case"FormCallMeBackSecond":
          return {"component":radioFirts,imgBG: `url(https://picsum.photos/1920/1080)` }
        case"CardMapCustom1":
          return {"component":radioFirts,
          "mainTitle": Content.LoremContent, "mainContent":Content.LoremContent3 ,
          "mainTitle1": Content.LoremContent, "mainContent1":Content.LoremContent3 ,
          "mainTitle2": Content.LoremContent, "mainContent2":Content.LoremContent3 , 
          "mainTitle3": Content.LoremContent, "mainContent3":Content.LoremContent3 , 
          "mainTitle4": Content.LoremContent, "mainContent4":Content.LoremContent3 , 
          "mainTitle5": Content.LoremContent, "mainContent5":Content.LoremContent3 , 
          "mainTitle6": Content.LoremContent, "mainContent6":Content.LoremContent3 , 
          imgBG: `url(https://picsum.photos/1920/1080)` ,
           img1: `https://picsum.photos/500/500`,  img2: `https://picsum.photos/500/500`,
           img3: `https://picsum.photos/500/500`,  img4: `https://picsum.photos/500/500`,
           img5: `https://picsum.photos/500/500`,img6: `https://picsum.photos/500/500`, }
        case"Accordion":
          return {"component":radioFirts,
          "mainTitle1": Content.LoremContent, "mainContent1":Content.LoremContent3 ,
          "mainTitle2": Content.LoremContent, "mainContent2":Content.LoremContent3 , 
          "mainTitle3": Content.LoremContent, "mainContent3":Content.LoremContent3 , 
          imgBG: `url(https://picsum.photos/1920/1080)` }
        
      }
    }

    function addLayout(){//    
      if(radioFirts!== undefined ){//     ,  
        let newlayout = layoutsTest.layouts //     ,     
        ReturnComponentBasedOnId();//        
        if(isEmptyObject(layoutsTest.layouts)){//     
          newlayout= {[breakPointCurrent]:[] }
          newlayout[breakPointCurrent].push(//  
            {
              "w":12,"h":10,"x":0,"y":0,"i":Number(counter),"static":false, minW: 2//     
            }
          )
        }else{//     
          for (const [key, value] of Object.entries(layoutsTest.layouts)) {//           
            newlayout[key].push(
              {
                "w":12,"h":11,"x":0,"y":4,"i":Number(counter),"static":false, minW: 2,
              }
            )
          }
        }
        setCounter(Number(counter)+1);// ,       index
        setLayoutsTest({layouts:newlayout})//  
        }else{
          setError("    .")
        }
    }

    function MapDiv(){ //        
      try{
      if(layoutsTest.layouts[breakPointCurrent] !== undefined){ //   , 
              return layoutsTest.layouts[breakPointCurrent].map((item)=>{// map             
                    return(
                      
                        <Grid  key={item.i} data-grid={item} style={{maxWidth:"inherit", transform:"translate(0px, 0px)", overflow:"hidden"}}>   {/*         */}
                        
                          {WhichComponentReturn(item.i)} {/*         */}
                          {Buttons(item.i)}{/*     / */}
                        </Grid>
                    )
                })
            }
      }catch(err){

      }
      
    }

    function Buttons(id){ //     /
      if(adminMode){//    ,    
        return(
        <div  style={{position:"absolute", right:"10px", top:"5%", }} >
          <Tooltip title=" " placement="left-start">
            <IconButton id={id}  onClick={toggleDrawerEdit("left", true)}>
                <SettingsIcon sx={{ color: "pink" }}/>
            </IconButton>
          </Tooltip>
          <Tooltip title=" " placement="left-start">
            <IconButton id={id} onClick={DeleteLayout} >
                <CloseIcon sx={{ color: "pink" }}/>
            </IconButton>
          </Tooltip>
        </div>)
      }
    }

    const toggleDrawerEdit = (anchor, open) => (event) => {//         
      if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
        return;
      }
      const index = event.currentTarget.getAttribute("id");
      if(index !== null){//        ,   
        setCurrentEdit(index)//    
      }else{
        setCurrentEdit("")//       null
      }
      setSelectedFiles({}); //   
      setEdit({ ...state, [anchor]: open });///    
    };

    function WhichComponentReturn(index){
      //   ,      .     
      //  index           
      const currentComponent = layoutComponents[breakPointCurrent][index]
      switch(currentComponent.component){
        case"Paragraph":
          return(
            <Paragraph md={10} lg={12} sm={6}
              justify={"flex-start"}
              mainTitle={currentComponent.mainTitle}
              mainContent={currentComponent.mainContent} 
              >
                  <OverlayImage img={currentComponent.imgBG}/>
                              
            </Paragraph>
          )
        case "CardNews1":
          return(
            <CardNews1  mainTitle={currentComponent.mainTitle}  img1={currentComponent.img1} img2={currentComponent.img2}
             mainContent1={currentComponent.mainContent1} mainContent2={currentComponent.mainContent2} >
                <OverlayImage img={currentComponent.imgBG}/>
                <OverlayFilling component_index={''}/>
            </CardNews1>
          )
        case "CardMap1":
          return(
            <CardMap1 img1={currentComponent.img1} img2={currentComponent.img2} img3={currentComponent.img3}
              mainTitle1={currentComponent.mainTitle1}  mainContent1={currentComponent.mainContent1} 
              mainTitle2={currentComponent.mainTitle2}  mainContent2={currentComponent.mainContent2}
              mainTitle3={currentComponent.mainTitle3}  mainContent3={currentComponent.mainContent3}
            >
                <OverlayImage img={currentComponent.imgBG}/>
                <OverlayFilling component_index={''}/>
            </CardMap1>
          )
        
        case"ProductHero":
            return(
              <ComponentBoxWithRoot>
                  <ProductHero  mainTitle={currentComponent.mainTitle}
                            
                              imgBG={currentComponent.imgBG}
                />
              </ComponentBoxWithRoot>
              
            )
        case"ProductHowItWorks":
            return(
              <ComponentBoxWithRoot>
              <ProductHowItWorks 
              mainTitle={currentComponent.mainTitle}  
              mainContent1={currentComponent.mainContent1} 
              mainContent2={currentComponent.mainContent2}
              mainContent3={currentComponent.mainContent3}
              imgBG={currentComponent.imgBG}
              />
               </ComponentBoxWithRoot>
            )
        case"ProductSmokingHero":
            return(
              <ComponentBoxWithRoot>
              <Grid style={{backgroundImage:currentComponent.imgBG,
                position: "absolute",
                top: 0,
                bottom: 0,
                right: 0,
                left: 0,
                borderRadius: "inherit",
                backgroundSize: "cover",
                backgroundRepeat: "no-repeat",
                backgroundPosition: "center",
              
              }}>
                <ProductSmokingHero 
                  mainTitle={currentComponent.mainTitle}
                  mainContent={currentComponent.mainContent}
                  />
              </Grid>
              </ComponentBoxWithRoot>
            )
        case"ProductCTA":
            return(
              <ComponentBoxWithRoot>
              <Grid style={{backgroundImage:currentComponent.imgBG,
                position: "absolute",
                top: 0,
                bottom: 0,
                right: 0,
                left: 0,
                borderRadius: "inherit",
                backgroundSize: "cover",
                backgroundRepeat: "no-repeat",
                backgroundPosition: "center",
              
              }}>
                <ProductCTA 
                mainTitle={currentComponent.mainTitle}
                mainContent={currentComponent.mainContent} 
                img1={currentComponent.img1} 
              />
              </Grid>
              </ComponentBoxWithRoot>
            )
          case "CardBoard":
            return(
              <CardBoard1 
               mainTitle1={currentComponent.mainTitle1}   mainContent1={currentComponent.mainContent1}
               mainTitle2={currentComponent.mainTitle2}   mainContent2={currentComponent.mainContent2}
               mainTitle3={currentComponent.mainTitle3}   mainContent3={currentComponent.mainContent3}
               mainTitle4={currentComponent.mainTitle4}   mainContent4={currentComponent.mainContent4}
               mainTitle5={currentComponent.mainTitle5}   mainContent5={currentComponent.mainContent5}
               mainTitle6={currentComponent.mainTitle6}   mainContent6={currentComponent.mainContent6}
               mainTitle7={currentComponent.mainTitle7}   mainContent7={currentComponent.mainContent7}
               mainTitle8={currentComponent.mainTitle8}   mainContent8={currentComponent.mainContent8}
               >
                  <OverlayImage img={currentComponent.imgBG}/>
              </CardBoard1>
            )
          case "Comments":
            return(
              <Comments3 
                mainTitle1={currentComponent.mainTitle1}   mainContent1={currentComponent.mainContent1}
                mainTitle2={currentComponent.mainTitle2}   mainContent2={currentComponent.mainContent2}
                mainTitle3={currentComponent.mainTitle3}   mainContent3={currentComponent.mainContent3}
                img1={currentComponent.img1} img2={currentComponent.img2} img3={currentComponent.img3}
                >
                  <OverlayImage img={currentComponent.imgBG}/>
                  <OverlayFilling/>
              </Comments3>
            )
          case "FormCallMeBackSecond":
            return(
              <FormCallMeBackSecond>
                <OverlayImage img={currentComponent.imgBG}/>
                <OverlayFilling/>
              </FormCallMeBackSecond>
            )
          case "CardMapCustom1":
            return(
              <WidgetBox>
                  <OverlayImage img={currentComponent.imgBG}/>
                  <CardMapCustom1 sm={6} md={4} lg={4} xl={4} justify={"flex-start"} spacing={3} partPrimary={8} partSecondary={4}
                    mainTitle1={currentComponent.mainTitle1}   mainContent1={currentComponent.mainContent1}
                    mainTitle2={currentComponent.mainTitle2}   mainContent2={currentComponent.mainContent2}
                    mainTitle3={currentComponent.mainTitle3}   mainContent3={currentComponent.mainContent3}
                    mainTitle4={currentComponent.mainTitle4}   mainContent4={currentComponent.mainContent4}
                    mainTitle5={currentComponent.mainTitle5}   mainContent5={currentComponent.mainContent5}
                    mainTitle6={currentComponent.mainTitle6}   mainContent6={currentComponent.mainContent6}
                    img1={currentComponent.img1} img2={currentComponent.img2} img3={currentComponent.img3} 
                    img4={currentComponent.img4} img5={currentComponent.img5} img6={currentComponent.img6} 
                  >
                     
                      <Paragraph md={12} justify={"center"} 
                       mainTitle={currentComponent.mainTitle}
                       mainContent={currentComponent.mainContent} 
                      >
                        
                        </Paragraph>
                  </CardMapCustom1>
              </WidgetBox>
            )
          case "Accordion":
            return(
             <div style={{height:"inherit",
                backgroundImage:currentComponent.imgBG,
                position: "absolute",
                top: 0,
                bottom: 0,
                right: 0,
                left: 0,
                borderRadius: "inherit",
                backgroundSize: "cover",
                backgroundRepeat: "no-repeat",
                backgroundPosition: "center",
                }}>
                 <AccordionMy
                  mainTitle1={currentComponent.mainTitle1}   mainContent1={currentComponent.mainContent1}
                  mainTitle2={currentComponent.mainTitle2}   mainContent2={currentComponent.mainContent2}
                  mainTitle3={currentComponent.mainTitle3}   mainContent3={currentComponent.mainContent3}
                  />
              </div>    
            )
      }
    }

    function DeleteLayout(event){//  
      const index = event.currentTarget.getAttribute("id");//        
      let newLayout=layoutsTest.layouts;//   ,       
      let newlayoutComponents = layoutComponents//   ,       
      for (const [key, value] of Object.entries(layoutsTest.layouts)) { //    

        for (const [key2, value2] of Object.entries(value)) {//     
          if(value2.i === index){//      ,       
            newLayout[key].splice(key2);
          }else{
            newLayout[key].push(value2)//   
          } 
        }        
      }
      for (const [key, value] of Object.entries(layoutComponents)) {//     
        delete newlayoutComponents[key][index];  //      
      }
      setLayoutsTest({layouts:newLayout});// 
      setLayoutcomponents(newlayoutComponents);// 
      global.localStorage.setItem("components",JSON.stringify(newlayoutComponents));//   LS 
    }
    
    function getFromLS(key) {//      LS 
        let ls = {};
        if (global.localStorage) {
          try {
            ls = JSON.parse(global.localStorage.getItem("rgl-8")) || {};
          } catch (e) {
            /*Ignore*/
          }
        }
        return ls[key];
    }

    function saveToLS(key, value) {//      LS 
        if (global.localStorage) {
          global.localStorage.setItem(
            "rgl-8",
            JSON.stringify({
              [key]: value
            })
          );
        }
    }

    function onLayoutChange(layout, layouts) { //    ,    
      try{
        saveToLS("layouts", layouts);
      }catch(err){

      }
     
      setLayoutsTest({layouts})
    }

  function onBreakpointChange(breakpoint, cols) {//    ,    
    setBreakPointCurrent(breakpoint);
  }

  function ReturnDrawerWidth(){//           
    switch(breakPointCurrent){
      case "lg":
        return "25%"
      case "md":
        return "30%"
      case"sm":
        return "40%"
      case "xs":
        return "90%"
      case "xxs":
        return "90%"
    }
  }

  function returnAddIcon(){
    if(adminMode){
    return(
      <Tooltip className={classes.fab} title=" " placement="left-start">
        <div>
          <Fab className={classes.fab} color={'primary'} onClick={toggleDrawer("left", true)}> 
            <AddIcon/>
          </Fab> 
        </div>
      </Tooltip>
    )
      
    }
  }


    return(
        <ThemeProvider theme={theme}>
                  <AppAppBar/>
                  <main className={classes.container} style={{width:"100%", overflow:"hidden"}}>
                
                    <div>                     
                      <Drawer anchor={"left"} open={state["left"]} onClose={toggleDrawer("left", false)} PaperProps={{
                        style: {width:ReturnDrawerWidth()}
                      }}>
                        {ChoseComponentList("left")}
                       {/*      */}
                      </Drawer>
                      <Drawer id='Edit' anchor={"left"} open={Edit["left"]} onClose={toggleDrawerEdit("left", false)} PaperProps={{
                        style: {width:ReturnDrawerWidth()}
                      }}>
                          {/*       */}
                        {EditText("left")}
                      </Drawer>
                    </div>
                  
                    <Grid item >
                      {/*    */}
                    <Switch
                        checked={adminMode}
                        onChange={handleChangeSwitch}
                        inputProps={{ 'aria-label': 'controlled' }}
                        style={{color:"rgb(40, 40, 42)"}}
                      />
                    </Grid>
                    <ResponsiveGridLayout
                    //       ,     
                      layouts={layoutsTest.layouts}
                      useCSSTransforms={false}
                      rowHeight={30}
                      margin={[0,0]}
                      cols={{ lg: 12, md: 10, sm: 6, xs: 4, xxs: 2 }}
                      onLayoutChange={(layout, layouts) =>
                          onLayoutChange(layout, layouts)
                        }
                      onBreakpointChange={onBreakpointChange}
                      >
                      
                      {MapDiv()}
                    </ResponsiveGridLayout>
                  
                  </main>
                  {/* {adminMode? <Tooltip className={classes.fab} title=" "> <div><Fab className={classes.fab} color={'primary'} onClick={toggleDrawer("left", true)}> <AddIcon/></Fab> </div> </Tooltip>:<></>}    */}
                  {returnAddIcon()}
                  
               {/* <AppFooter/> */}
               <Footer2/>
        </ThemeProvider>
    )
}

export default Wrapper;
